.city {
	height: auto;
	width: 500px;
	margin: 20px;
	background-color: white;
	font-family: Roboto;
	border-radius: 3px;
}
.city:hover {
	transform: scale(1.03, 1.03);
	transition: all 0.2s ease-in-out;
}
.buttons p {
	padding: 20px;
}

.home-img {
	width: 100%;
	margin: none;
	font-family: Roboto;
	padding: none;
}
.rent-area {
	background-color: #03c5f0;
	height: 40px;
	color: white;
	display: flex;
	justify-content: space-around;
	align-items: center;
	font-family: Roboto;
	/* flex-direction: column; */
}
.bedroom-area {
	background-color: white;
	height: auto;
	color: black;
	display: flex;
	justify-content: center;
	align-content: center;
	flex-direction: column;
	font-size: 17px;
	font-family: Roboto;
}

.address-area {
	background-color: white;
	height: auto;
	color: black;
	display: flex;
	justify-content: center;
	align-content: center;
	flex-direction: column;
	font-size: 15px;
	font-family: Roboto;
}
.bed-icon {
	fill: white;
	padding-top: 6px;
	margin-left: 20px;
}
.rent-text {
	font-size: 20px;
	padding: 0;
	padding-left: 20px;
}
.rent-span {
	font-size: 27px;
}

.buttons {
	height: 50;
	display: flex;
	border-top: solid 1px #e5e5e5;
	align-items: center;
	justify-content: center;
	font-family: Roboto;
}
.shortlist-btn {
	flex: 6;
	height: auto;
	border-right: solid 1px #e5e5e5;
	font-size: 24px;
	text-align: center;
	font-family: Roboto;
	cursor: pointer;
}

.shortlist-btn .a-btn {
	display: none;
	padding: 10px;
}
.shortlist-btn:hover .s-btn {
	display: none;
}
.shortlist-btn:hover .a-btn {
	display: inline;
	color: red;
}
.shortlist-btn:hover .heart-icon {
	fill: red;
}

.view-btn {
	flex: 6;
	height: auto;
	font-size: 24px;
	text-align: center;
	font-family: Roboto;
	cursor: pointer;
	color: inherit;
}
.rem-btn {
	flex: 6;
	height: auto;
	font-size: 24px;
	text-align: center;
	font-family: Roboto;
	color: red;
	cursor: pointer;
}
.s-btn {
	font-size: 24px;
	padding: 3px;
}
.num-of-bed {
	font-size: 22px;
	margin-right: 10px;
}
.num-of-bath {
	font-size: 22px;
	margin-right: 8px;
}

